<template>
    <div class="matchBusinessData">
        <a-spin tip="加载中..." :spinning="spinning" size="large">
            <div style="text-align: center; margin-bottom: 8px">
                <a-icon type="tag" style="margin-right: 8px" />
                {{ `${totalCount}条数据` }}
                <span
                    @click="manualRefesh"
                    style="color: #409eff; margin-left: 10px; cursor: pointer"
                >
                    <a-icon type="sync" style="margin-right: 5px" />刷新
                </span>
                {{ `  (已关联工商数据${matchCount}条)` }} |
                {{ `含抽凭数据${samplingCount}条`
                }}{{ `  (已关联工商数据${samplingMatchCount}条)` }}
                <a-button type="primary" @click="unmatchedBusinessData">
                    未匹配工商数据
                </a-button>
                <a-checkbox
                    v-model="checked"
                    style="margin-left: 10px"
                    @change="onChange"
                    >显示</a-checkbox
                >
            </div>
            <a-table
                :columns="!changeTable ? columns : columns2"
                :data-source="dataList"
                bordered
                :rowKey="(row) => row.key"
                :pagination="pagination"
                :scroll="{ y: 'calc(100% - 55px)', x: 'scollXWidth' }"
                  :components='components'

            >
                <template
                    slot="customerUscc"
                    slot-scope="text, record"
                    v-if="!changeTable"
                >
                    <editable-cell
                        :text="text"
                        @change="onCellChange(record, 'customerUscc', $event)"
                    />
                    <a-button
                        v-if="record.customerUscc"
                        type="primary"
                        shape="circle"
                        icon="sync"
                        @click="updataBusiness(record.customerUscc)"
                        size="small"
                        title="更新工商数据"
                    />
                    <a-button
                        v-if="record.customerUscc"
                        type="primary"
                        shape="circle"
                        icon="delete"
                        @click="clearUscc(record, 'customerUscc', '')"
                        size="small"
                        title="一键清除"
                    />
                </template>
            </a-table>
        </a-spin>
        <a-modal
            v-model="searchDialogVisible"
            title="查询"
            ok-text="确认"
            cancel-text="取消"
            @ok="onSubmitSeachItem"
            @cancel="cancelSearch"
        >
            <a-form-model
                :model="searchForm"
                :label-col="labelCol"
                :wrapper-col="wrapperCol"
            >
                <a-form-model-item label="客商名称">
                    <a-input
                        v-model="searchForm.merchantName"
                        placeholder="请输入"
                    />
                </a-form-model-item>
                <a-form-model-item label="含抽凭数据">
                    <a-select
                        v-model="searchForm.isSampling"
                        placeholder="请选择"
                    >
                        <a-select-option
                            :value="item.value"
                            v-for="item in isSamplingList"
                            :key="item.key"
                        >
                            {{ item.label }}
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
                <a-form-model-item label="统一信用代码">
                    <a-input
                        v-model="searchForm.customerUscc"
                        placeholder="请输入"
                    />
                </a-form-model-item>
            </a-form-model>
        </a-modal>
    </div>
</template>
<script>
import XEUtils from 'xe-utils';
import { formatter } from '../libs/utils/formatter';
import session from '@data/session/sessionUtils.js';

import Vue from 'vue';
import VueDraggableResizable from 'vue-draggable-resizable';
Vue.component('vue-draggable-resizable', VueDraggableResizable);

const EditableCell = {
    template: `
        <div class="editable-cell">
            <div v-if="editable" class="editable-cell-input-wrapper">
            <a-input :value="value" @change="handleChange" @pressEnter="check" /><a-icon
                type="check"
                class="editable-cell-icon-check"
                @click="check"
            />
            </div>
            <div v-else class="editable-cell-text-wrapper">
            {{ value || ' ' }}
            <a-icon type="edit" class="editable-cell-icon" @click="edit" />
            </div>
        </div>
        `,
    props: {
        text: String,
    },
    data() {
        return {
            value: '',
            editable: false,
        };
    },
    watch: {
        text: {
            handler(val, oldVal) {
                this.value = val;
            },
            immediate: true,
        },
    },
    methods: {
        handleChange(e) {
            const value = e.target.value;
            this.value = value;
            // this.$set(this.value,value)
        },
        check() {
            this.editable = false;
            this.$emit('change', this.value);
        },
        edit() {
            this.editable = true;
        },
    },
};
export default {
    components: {
        EditableCell,
    },
    name: 'matchBusinessData',
    data() {
             this.components = {
      header: {
        cell: (h, props, children) => {
          const { key, ...restProps } = props
          const col = this.columns.find(col => {
            const k = col.dataIndex || col.key
            return k === key
          })
 
          if (!col || !col.width) {
            return h('th', { ...restProps }, [...children])
          }
 
          const dragProps = {
            key: col.dataIndex || col.key,
            class: 'table-draggable-handle',
            attrs: {
              w: 10,
              x: col.width,
              z: 1,
              axis: 'x',
              draggable: true,
              resizable: false
            },
            on: {
              dragging: (x, y) => {
                col.width = Math.max(x, 1)
              }
            }
          }
          const drag = h('vue-draggable-resizable', { ...dragProps })
          return h('th', { ...restProps, class: 'resize-table-th' }, [...children, drag])
        }
      }
    }
        return {
            scollXWidth: 3000,
            value: this.text,
            editable: false,
            searchDialogVisible: false,
            spinning: false,
            columns: [
                // {
                //     title: '客商编号',
                //     dataIndex: 'merchantId',
                //     width:'170px',
                //     ellipsis: true,
                //     fixed: 'left'
                // },
                {
                    title: '客商名称',
                    dataIndex: 'merchantName',
                    width: 300,
                    ellipsis: true,
                    fixed: 'left',
                },
                {
                    title: '含抽凭数据',
                    dataIndex: 'isSampling',
                    ellipsis: true,
                    width:120,
                    scopedSlots: { customRender: 'isSampling' },
                },
                {
                    title: '一级科目',
                    dataIndex: 'oneSubjectName',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '科目全路径',
                    dataIndex: 'subjectFullName',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '期初余额',
                    dataIndex: 'remain',
                    width: 170,
                    ellipsis: true,
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '借方发生额',
                    dataIndex: 'debitOcc',
                    width: 170,
                    ellipsis: true,
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方发生额',
                    dataIndex: 'creditOcc',
                    width:170,
                    ellipsis: true,
                    align: 'right',
                    customRender: formatter.commafy,
                },
                {
                    title: '期末余额',
                    dataIndex: 'balance',
                    ellipsis: true,
                    align: 'right',
                    width: 170,
                    // width:'160px',
                    customRender: formatter.commafy,
                },
                {
                    title: '统一信用代码',
                    dataIndex: 'customerUscc',
                    width: 270,
                    ellipsis: true,
                    scopedSlots: { customRender: 'customerUscc' },
                    customCell: (record, rowIndex) => {
                        return {
                            style: {
                                'background-color': '#E0F5E9',
                            },
                        };
                    },
                },
                {
                    title: '工商注册名称',
                    dataIndex: 'customerName',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '工商办公地址',
                    dataIndex: 'officeAddress',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '工商注册地址',
                    dataIndex: 'registeredAddress',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '工商注册资本',
                    width: 200,
                    dataIndex: 'registerAmount',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '百度地址',
                    dataIndex: 'baiduAddress',
                    width: 200,
                    ellipsis: true,
                },
                {
                    title: '地区',
                    dataIndex: 'areaPlace',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'areaPlace' },
                },
                {
                    title: '行业大类',
                    dataIndex: 'industrycategory',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'industrycategory' },
                },
                {
                    title: '行业门类',
                    dataIndex: 'industryclassification',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'industryclassification' },
                },
            ],
            isSamplingList: [
                { label: '全部', key: 0, value: '' },
                { label: '是', key: 1, value: '1' },
                { label: '否', key: 2, value: '0' },
            ],
            labelCol: { span: 6 },
            wrapperCol: { span: 13 },
            dataList: [],
            dataListCopy: [],
            changeTable: false,
            columns2: [
                {
                    title: '统一信用代码',
                    dataIndex: 'customerUscc',
                    width: '200px',
                    ellipsis: true,
                    fixed: 'left',
                    customRender: (value, row, index) => {
                        const obj = {
                            children: value,
                            attrs: {},
                        };
                        const arrResult = formatter.mergeCells(
                            this.dataList,
                            'customerUscc'
                        );
                        for (let i = 0; i < arrResult[0].length; i++) {
                            if (index === arrResult[1][i]) {
                                obj.attrs.rowSpan = arrResult[0][i];
                            }
                            if (
                                index > arrResult[1][i] &&
                                index <= arrResult[1][i + 1]
                            ) {
                                obj.attrs.rowSpan = 0;
                            }
                        }
                        return obj;
                    },
                },
                {
                    title: '工商注册名称',
                    dataIndex: 'customerName',
                    width: '300px',
                    ellipsis: true,
                    // fixed: 'left',
                    customRender: (value, row, index) => {
                        const obj = {
                            children: value,
                            attrs: {},
                        };
                        const arrResult = formatter.mergeCells(
                            this.dataList,
                            'customerName'
                        );
                        for (let i = 0; i < arrResult[0].length; i++) {
                            if (index === arrResult[1][i]) {
                                obj.attrs.rowSpan = arrResult[0][i];
                            }
                            if (
                                index > arrResult[1][i] &&
                                index <= arrResult[1][i + 1]
                            ) {
                                obj.attrs.rowSpan = 0;
                            }
                        }
                        return obj;
                    },
                },
                {
                    title: '工商办公地址',
                    width: '200px',
                    dataIndex: 'officeAddress',
                    ellipsis: true,
                },
                {
                    title: '工商注册地址',
                    width: '200px',
                    dataIndex: 'registeredAddress',
                    ellipsis: true,
                },
                {
                    title: '工商注册资本',
                    width: 200,
                    dataIndex: 'registerAmount',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '百度地址',
                    width: '200px',
                    dataIndex: 'baiduAddress',
                    ellipsis: true,
                },
                {
                    title: '客商编号',
                    dataIndex: 'merchantId',
                    width: '170px',
                    ellipsis: true,
                },
                {
                    title: '客商名称',
                    width: '300px',
                    dataIndex: 'merchantName',
                    ellipsis: true,
                },
                {
                    title: '含抽凭数据',
                    dataIndex: 'isSampling',
                    width: '120px',
                    scopedSlots: { customRender: 'isSampling' },
                },
                {
                    title: '一级科目',
                    dataIndex: 'subjectName',
                    width: '200px',
                    ellipsis: true,
                },
                {
                    title: '地区',
                    dataIndex: 'areaPlace',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'areaPlace' },
                },
                {
                    title: '行业大类',
                    dataIndex: 'industrycategory',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'industrycategory' },
                },
                {
                    title: '行业门类',
                    dataIndex: 'industryclassification',
                    width: 170,
                    ellipsis: true,
                    scopedSlots: { customRender: 'industryclassification' },
                },
                {
                    title: '期初余额',
                    dataIndex: 'remain',
                    width: '170px',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '借方发生额',
                    width: '170px',
                    dataIndex: 'debitOcc',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '贷方发生额',
                    width: '170px',
                    dataIndex: 'creditOcc',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
                {
                    title: '期末余',
                    width: '170px',
                    dataIndex: 'balance',
                    align: 'right',
                    ellipsis: true,
                    customRender: formatter.commafy,
                },
            ],
            queryParams: {
                sqlId: 'DG40003',
                start: 0,
                limit: 10,
                param9: '',
            },
            searchForm: {
                merchantName: null,
                isSampling: null,
                customerUscc: null,
            },
            pagination: {
                current: 1,
                pageSize: 10,
                showSizeChanger: true,
                pageSizeOptions: ['10', '20', '30', '40', '50'],
                onShowSizeChange: (current, pageSize) => {
                    this.queryParams.start = current - 1;
                    this.queryParams.limit = pageSize;
                    this.queryParams.isChange = true;
                    this.pagination.pageSize = pageSize;
                    this.queryMerchantInitData();
                },
                showQuickJumper: false,
                size: 'small',
                total: 0,
                showTotal: () => `共${this.pagination.total}条`,
                change: true,
                onChange: (current, size) => {
                    //start 0 -10 -20 -30
                    this.queryParams.start = (current - 1) * size;
                    this.queryParams.limit = this.pagination.pageSize;
                    this.queryParams.isChange = false;
                    this.pagination.current = current;
                    this.queryMerchantInitData();
                },
            },
            totalCount: 0,
            matchCount: 0,
            samplingCount: 0,
            samplingMatchCount: 0,
            checked: false,
        };
    },
    methods: {
        updataBusiness(uscc) {
            if (this.spinning) {
                return this.$message.error('数据处理中,请勿重复点击');
            }

            let par = {
                param1: uscc,
            };
            let that = this;

            this.$confirm({
                title: `提示`,
                content: '是否确定要更新工商数据？',
                onOk() {
                    that.spinning = true;
                    that.$sacpApis
                        .updateCompanyByUscc(par)
                        .then((res) => {
                            if (res.success) {
                                that.queryMerchantInitData();
                            }
                        })
                        .catch((err) => that.$message.error(err))
                        .finally(() => (that.spinning = false));
                },
                onCancel() {},
            });
        },
        clearUscc(record, dataIndex, value) {
            this.$confirm({
                title: `提示`,
                content: '是否清除该统一信用代码？',
                onOk: () => {
                    this.onCellChange(record, dataIndex, value);
                },
                onCancel: () => {},
            });
        },
        matchBaiduAddress() {
            //获取百度地址
            this.spinning = true;
            this.$sacpApis
                .matchBaiduAddressByHand()
                .then((res) => {
                    if (res.success) {
                        this.queryMerchantInitData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        matchCompanyChange() {
            this.changeTable = !this.changeTable;
            if (!this.changeTable) {
                this.queryParams.sqlId = 'DG40003';
            } else {
                this.queryParams.sqlId = 'DG40004';
            }
            this.queryMerchantInitData();
        },
        openSearchDialog() {
            this.searchDialogVisible = true;
        },
        onCellChange(row, dataIndex, value) {
            const dataSource = [...this.dataList];
            const target = dataSource.find((item) => item.key === row.key);
            if (target[dataIndex] == value)
                return this.$message.warning('当前数据未改动!');
            target[dataIndex] = value;
            this.spinning = true;
            if (target) {
                let params = {
                    param1: target.autoId,
                    param2: value,
                };
                this.$sacpApis
                    .matchCompanyByUscc(params)
                    .then((res) => {
                        const dataSource = [...this.dataListCopy];
                        const targetObj = dataSource.find(
                            (item) => item.key === row.key
                        );
                        if (res.success) {
                            const clearField = [
                                'customerName',
                                'officeAddress',
                                'registeredAddress',
                                'registerAmount',
                                'baiduAddress',
                                'areaPlace',
                                'industrycategory',
                                'industryclassification',
                            ];
                            targetObj.customerUscc = value;
                            clearField.forEach((x) => {
                                targetObj[x] = '';
                                target[x] = '';
                            });
                            this.$message.success('统一信用代码修改成功');
                        } else {
                            row.customerUscc = targetObj.customerUscc;
                            this.$message.error(res.resultInfo.statusText);
                        }
                    })
                    .catch((err) => this.$message.error(err))
                    .finally(() => {
                        // this.queryMerchantInitData();
                        this.spinning = false;
                    });
            }
        },
        onSubmitSeachItem() {
            this.queryParams.start = 0;
            this.queryParams.limit = 10;
            this.queryParams.isChange = true;
            XEUtils.set(
                this.queryParams,
                'param3',
                this.searchForm.merchantName
            );
            XEUtils.set(this.queryParams, 'param4', this.searchForm.isSampling);
            XEUtils.set(
                this.queryParams,
                'param5',
                this.searchForm.customerUscc
            );
            this.queryMerchantInitData();
        },
        cancelSearch() {
            this.searchDialogVisible = false;
            Object.keys(this.searchForm).forEach(
                (key) => (this.searchForm[key] = null)
            );
        },
        queryMatchCompanyList() {
            this.spinning = true;
            this.$sacpApis
                .matchCompanyList()
                .then((res) => {
                    if (res.success) {
                        this.queryMerchantInitData();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                    this.spinning = false;
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.isChange = false;
                });
        },
        unmatchedBusinessData() {
            this.queryParams.param9 = 1;
            this.queryMerchantInitData();
        },
        queryMerchantInitData() {
            this.queryParams.lockCustomerId =
                session.findUserInfo().curCustomerId;
            this.queryParams.lockProjectId =
                session.findUserInfo().curProjectId;
            this.queryParams.lockYyyy = session.findUserInfo().curAccYear;
            this.spinning = true;
            this.dataList.length = 0;
            this.queryParams.param10 = this.checked ? '' : 1;
            this.$sacpApis
                .findMerchantList(this.queryParams)
                .then((res) => {
                    if (res.success) {
                        if (res.data && res.data.length)
                            res.data.forEach((item, index) => {
                                XEUtils.set(item, 'key', index);
                            });
                        this.dataList = res.data;
                        this.dataListCopy = JSON.parse(
                            JSON.stringify(res.data)
                        );
                        this.pagination.defaultPageSize = res.start;
                        this.pagination.total = res.totalCount;
                        this.totalCount = res.totalCount;
                        if (this.queryParams.isChange)
                            this.pagination.current = 1; //分页回到第一页
                        this.queryMatchNum();
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    if (this.searchDialogVisible) this.cancelSearch();
                    this.spinning = false;
                });
        },
        queryMatchNum() {
            this.$sacpApis
                .matchedNum()
                .then((res) => {
                    if (res.success) {
                        const data = res.data[0];
                        this.matchCount = data.matchCount;
                        this.samplingCount = data.samplingCount;
                        this.samplingMatchCount = data.samplingMatchCount;
                        this.totalCount = data.totalCount;
                    } else {
                        this.$message.error(res.resultInfo.statusText);
                    }
                })
                .catch((err) => this.$message.error(err))
                .finally(() => {
                    this.spinning = false;
                });
        },
        manualRefesh() {
            this.queryParams.param9 = '';
            this.queryMerchantInitData();
        },
        onChange() {
            this.queryMerchantInitData();
        },
    },
    created() {
        this.queryMerchantInitData();
    },
};
</script>

<style lang="scss" scoped>
.editable-cell {
    display: inline-block;
    width: 180px;
}
.matchBusinessData {
    & >>> .ant-spin-container {
        height: 99%;
    }
}
.resize-table-th {
  position: relative;
}
.table-draggable-handle {
  transform: none;
  position: absolute;
  height: 100% !important;
  left: auto !important;
  right: -5px;
  cursor: col-resize;
  touch-action: none;
  border: none;
}

</style>


